document.addEventListener( "plusready",  function()
{
    var _BARCODE = 'faceplugin',
		B = window.plus.bridge;
    var facePlug =
    {
    	faceliveness : function (Argus1, successCallback, errorCallback )
    	{
    		var success = typeof successCallback !== 'function' ? null : function(args) 
    		{
    			successCallback(args);
    		},
    		fail = typeof errorCallback !== 'function' ? null : function(code) 
    		{
    			errorCallback(code);
    		};
    		callbackID = B.callbackId(success, fail);
    	
    		return B.exec(_BARCODE, "faceliveness", [callbackID, Argus1]);
    	},
		recycler : function (Argus1, successCallback, errorCallback )
		{
			var success = typeof successCallback !== 'function' ? null : function(args) 
			{
				successCallback(args);
			},
			fail = typeof errorCallback !== 'function' ? null : function(code) 
			{
				errorCallback(code);
			};
			callbackID = B.callbackId(success, fail);
		
			return B.exec(_BARCODE, "recycler", [callbackID, Argus1]);
		}
    };
    window.plus.facePlug = facePlug;
}, true );